import { useState, useCallback } from 'react';
import { useNavigate } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import { useForm } from 'react-hook-form';
import cn from 'classnames';
import { Input } from '../UI/Input/Input';
import { Button } from '../UI/Button/Button';
import { ButtonTypeStyle as bts } from '../UI/Button/Button.utils';
import { userRegistration } from '../../Models/UserAuth/userAuth.actions';
import { validateOptions } from './RegistrationForm.utils';
import { errorsHelper } from '../../utils/helpers/errorsHelper';
import style from './RegistrationForm.module.css';

export const RegistrationForm = () => {
  const [login, setLogin] = useState('');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');

  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm({ mode: 'onBlur' });

  const dispatch = useDispatch();
  const navigate = useNavigate();

  const changeLogin = useCallback(
    e => {
      setLogin(e.target.value);
    },
    [setLogin]
  );

  const changeEmail = useCallback(
    e => {
      setEmail(e.target.value);
    },
    [setEmail]
  );

  const changePassword = useCallback(e => {
    setPassword(e.target.value);
  }, []);

  const buttonOnClick = useCallback(() => {
    navigate('/');
  }, []);

  const sendLoginRequest = data => {
    dispatch(userRegistration(data));
  };

  const errorsData = errorsHelper(errors, validateOptions, style);

  return (
    <form className={style.loginForm} onSubmit={handleSubmit(sendLoginRequest)}>
      <p className={style.title}></p>
      <Input
        value={login}
        placeholder=""
        className={cn(style.input, { [style.isError]: errorsData['username'] })}
        onChange={changeLogin}
        register={register}
        name="username"
        validateOption={validateOptions.username}
      />
      {errorsData['username']}

      <Input
        value={email}
        placeholder="Email"
        className={cn(style.input, { [style.isError]: errorsData['email'] })}
        onChange={changeEmail}
        name="email"
        register={register}
        validateOption={validateOptions.email}
      />
      {errorsData['email']}

      <Input
        value={password}
        type="password"
        placeholder=""
        className={cn(style.input, { [style.isError]: errorsData['password'] })}
        onChange={changePassword}
        register={register}
        name="password"
        validateOption={validateOptions.password}
      />
      {errorsData['password']}

      <div className={style.btnGroup}>
        <Button typeStyle={bts.SUCCESS} text="" submit />
        <Button
          typeStyle={bts.GREENSUCCESS}
          text=""
          onClick={buttonOnClick}
        />
      </div>
    </form>
  );
};
