/* eslint-disable */
import { useRef, useState } from 'react';
import { useDispatch } from 'react-redux';
import pt from 'prop-types';
import { useForm } from 'react-hook-form';
import { Button } from '../UI/Button/Button';
import { ButtonTypeStyle as btn } from '../UI/Button/Button.utils';
import { Input } from '../UI/Input/Input';
import { InputTypeStyle as its } from '../UI/Input/Input.utils';
import { TextArea } from '../UI/TextArea/TextArea';
import { libraryCardState } from './LibraryCard.utils';
import {
  createUserPostRequest,
  deleteUserPostRequest,
  updateUserPostRequest,
} from '../../Models/UserLibrariesItems/UserLibrariesItems.actions';
import style from './LibraryCard.module.css';
import { RadioButton } from '../UI/RadioButton/RadioButton';
import {
  radioButtonValue as rbv,
  defaultWatchStatus,
} from './LibraryCard.utils';
import cn from 'classnames';

export const LibraryCard = ({ data, closeCard }) => {
  const [radioValue, setRadioValue] = useState(
    data?.watchStatus || defaultWatchStatus
  );
  const [title, setTitle] = useState(data?.title || '');
  const [season, setSeason] = useState(data?.season || '');
  const [typeInfo, setTypeInfo] = useState(data?.typeInfo || '');
  const [genre, setGenre] = useState(data?.genre || '');
  const [studio, setStudio] = useState(data?.studio || '');
  const [author, setAuthor] = useState(data?.author || '');
  const [description, setDescription] = useState(data?.description || '');
  const [preview, setPreview] = useState(data?.image || '');
  const [cardState, setCardState] = useState(
    data ? libraryCardState.IS_VIEW : libraryCardState.IS_CREATE
  );
  const [favourite, setFavourite] = useState(data?.favourite || false);

  const inputRef = useRef();
  const dispatch = useDispatch();

  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm({ mode: 'onBlur' });

  const imageHelper = event => {
    const file = event.target.files[0];
    if (file && file.type.substr(0, 5) === 'image') {
      const reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = () => {
        setPreview(reader.result);
      };
    } else {
      setPreview(null);
    }
  };

  const createPost = dataRequest => {
    console.log('createPost:', {
      ...dataRequest,
      image: preview,
    });
    dispatch(
      createUserPostRequest({ ...dataRequest, image: preview, watchStatus: radioValue })
    );
    closeCard(false);
  };

  const updatePost = dataRequest => {
    dataRequest.id = data.id;
    dispatch(updateUserPostRequest({...dataRequest, image: preview, watchStatus: radioValue }));
    setCardState(libraryCardState.IS_VIEW);
  };

  const deletePost = () => {
    dispatch(deleteUserPostRequest(data.id));
    closeCard(false);
  };

  const isCreate = cardState === libraryCardState.IS_CREATE;
  const isView = cardState === libraryCardState.IS_VIEW;
  const isEdit = cardState === libraryCardState.IS_EDIT;
  const isDisabled = isView;
  const textSubmitButton = isCreate ? '' : ''

  const imageOnClick = event => {
    if (isDisabled) return;
    event.preventDefault();
    inputRef.current.click();
  };

  const sendCard = dataReq => {
    if (isCreate) {
      createPost(dataReq);
    } else {
      updatePost(dataReq);
    }
  };

  const radioChange = v => {
    setRadioValue({
      [rbv.WILL_WATCH]: v === rbv.WILL_WATCH,
      [rbv.WATCH]: v === rbv.WATCH,
      [rbv.WATCHED]: v === rbv.WATCHED,
      [rbv.ABANDONED]: v === rbv.ABANDONED,
    });
  };


  console.log('radioChange:', radioValue);
  console.log('favourite:', favourite);

  return (
    <form className={style.card} onSubmit={handleSubmit(sendCard)}>
      <Input
        name="title"
        value={title}
        className={style.name}
        typeStyle={its.TEXTCARD}
        onChange={e => setTitle(e.target.value)}
        placeholder=" "
        disabled={isDisabled}
        register={register}
      />
      <div className={style.shell}>
        <div className={style.info}>
          <input
            {...register('image')}
            ref={inputRef}
            type="file"
            name="image"
            className={style.btnHidden}
            text=" "
            onChange={imageHelper}
            accept="image/*"
          />
          <img
            className={style.picture}
            src={
              preview ||
              'https://catherineasquithgallery.com/uploads/posts/2021-02/1612462713_1-p-fon-serogo-tsveta-kartinki-1.jpg'
            }
            onClick={imageOnClick}
            alt=" "
            role="presentation"
          />
            <Button
              className={cn(style.btnFavourite)}
              typeStyle={favourite ? btn.GREENSUCCESS : btn.CANCEL}
              name={favourite}
              value={favourite}
              register={register}
              text=""
              onClick={e => setFavourite(!favourite)}
            />
        </div>
        <div className={style.textBlock}>
          <div className={style.text}>
            <div className={style.descriptionElement}>
              <p>:</p>
              <Input
                name="season"
                value={season}
                className={style.info}
                typeStyle={its.TEXTCARD}
                onChange={e => setSeason(e.target.value)}
                placeholder=" "
                disabled={isDisabled}
                register={register}
              />
            </div>
            <div className={style.descriptionElement}>
              <p>:</p>
              <Input
                name="typeInfo"
                value={typeInfo}
                className={style.info}
                typeStyle={its.TEXTCARD}
                onChange={e => setTypeInfo(e.target.value)}
                placeholder=" "
                disabled={isDisabled}
                register={register}
              />
            </div>
            <div className={style.descriptionElement}>
              <p>: </p>
              <Input
                name="genre"
                value={genre}
                className={style.info}
                typeStyle={its.TEXTCARD}
                onChange={e => setGenre(e.target.value)}
                placeholder=" "
                disabled={isDisabled}
                register={register}
              />
            </div>
            <div className={style.descriptionElement}>
              <p>: </p>
              <Input
                name="studio"
                value={studio}
                className={style.info}
                typeStyle={its.TEXTCARD}
                onChange={e => setStudio(e.target.value)}
                placeholder=" "
                disabled={isDisabled}
                register={register}
              />
            </div>
            <div className={style.descriptionElement}>
              <p>: </p>
              <Input
                className={style.info}
                register={register}
                value={author}
                name="author"
                typeStyle={its.TEXTCARD}
                onChange={e => setAuthor(e.target.value)}
                placeholder=" "
                disabled={isDisabled}
              />
            </div>
            <hr />
            <TextArea
              className={style.info2}
              register={register}
              value={description}
              name="description"
              type="textarea"
              wrap="hard"
              rows="20"
              cols="50"
              onChange={e => setDescription(e.target.value)}
              placeholder="  "
              disabled={isDisabled}
            />
          </div>
          <div className={style.btnGroup}>
            {(isCreate || isEdit) && (
              <Button
                className={style.btnAdd}
                typeStyle={btn.GREENSUCCESS}
                text={textSubmitButton}
                submit
              />
            )}
            {isView && (
              <Button
                className={style.btnAdd}
                typeStyle={btn.SUCCESS}
                text=""
                onClick={() => setCardState(libraryCardState.IS_EDIT)}
              />
            )}
            {isEdit && (
              <Button
                className={style.btnAdd}
                typeStyle={btn.CANCEL}
                text=""
                onClick={deletePost}
              />
            )}
          </div>
        </div>
      </div>

      <hr />
      <div className={style.interactive}>
        <div>
          <RadioButton
            name={rbv.WILL_WATCH}
            label=" "
            value={radioValue[rbv.WILL_WATCH]}
            disabled={isDisabled}
            onChange={radioChange.bind(this, rbv.WILL_WATCH)}
          />
        </div>
        <div>
          <RadioButton
            name={rbv.WATCH}
            label=""
            value={radioValue[rbv.WATCH]}
            disabled={isDisabled}
            onChange={radioChange.bind(this, rbv.WATCH)}
          />
        </div>
        <div>
          <RadioButton
            name={rbv.WATCHED}
            value={radioValue[rbv.WATCHED]}
            label=""
            disabled={isDisabled}
            onChange={radioChange.bind(this, rbv.WATCHED)}
          />
        </div>
        <div>
          <RadioButton
            name={rbv.ABANDONED}
            value={radioValue[rbv.ABANDONED]}
            label=""
            disabled={isDisabled}
            onChange={radioChange.bind(this, rbv.ABANDONED)}
          />
        </div>
      </div>
    </form>
  );
};

LibraryCard.propTypes = {
  data: pt.objectOf(pt.string),
  closeCard: pt.func,
};

LibraryCard.defaultProps = {
  data: {},
  closeCard: () => {},
};
