import { useCallback } from 'react';
import { useNavigate } from 'react-router-dom';
import { ButtonTypeStyle as bts } from '../../components/UI/Button/Button.utils';
import { Button } from '../../components/UI/Button/Button';
import style from './Profile.module.css';

export const Profile = () => {
  const navigate = useNavigate();

  const buttonOnClick = useCallback(() => {
    navigate('/');
  }, []);

  return (
    <div className={style.container}>
      <Button
        className={style.backBtn}
        typeStyle={bts.CANCEL}
        text=""
        onClick={buttonOnClick}
      />
      <div className={style.profile}>
        <div className={style.flexContainer}>
          <div className={style.item1}>
            <img role="presentation" className={style.item1__img} alt="" />
            <div className={style.item1__content}>
              <div className={style.item1__content__group}>
                <p className={style.item1__content__group__id}>ID 1123123</p>
                <p className={style.item1__content__group__name}>
                  NullPointerExeption
                </p>
                <p className={style.item1__content__group__status}> </p>
              </div>
              <p className={style.item1__content_time}> : 1682.</p>
            </div>
          </div>
          <div className={style.item2}>item2</div>
        </div>
        <div className={style.item3}>item3</div>
      </div>
    </div>
  );
};
