import axios from 'axios';
import { getCookie, changeTokenCookies } from '../utils/cookie';

const api = axios.create({
  baseURL: process.env.REACT_APP_BASE_URL,
  headers: {
    Accept: 'application/json',
    'Content-Type': 'application/json',
  },
});

api.interceptors.request.use(request => {
  request.headers.Authorization = `${getCookie('accessToken')}`;
  return request;
});

api.interceptors.response.use(
  response => response,
  async error => {
    const request = error.config;
    if (error.response.status === 401 && !error?.config.retry) {
      request.retry = true;

      try {
        const usersTokens = await api.post('auth/refresh', {
          refreshToken: `${getCookie('refreshToken')}`,
        });
        changeTokenCookies(usersTokens.data);
        return request;
      } catch (e) {
        changeTokenCookies({}, true);
      }
    }
    throw error;
  }
);

export default api;
