import { useCallback, useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { ItemLibraryCard } from '../../components/ItemLibraryCard/ItemLibraryCard';
import { Button } from '../../components/UI/Button/Button';
import { ButtonTypeStyle as bts } from '../../components/UI/Button/Button.utils';
import { UserInfoBlock } from '../../components/UserInfoBlock/UserInfoBlock';
import { userLogout } from '../../Models/UserAuth/userAuth.actions';
import { Modal } from '../../components/UI/Modal/Modal';
import { librariesItems } from '../../Models/UserLibrariesItems/UserLibrariesItems.selectors';
import { LibraryCard } from '../../components/LibraryCard/LibraryCard';
import style from './AnimLibrary.module.css';
import { getUserPostRequest } from '../../Models/UserLibrariesItems/UserLibrariesItems.actions';

export const AnimLibrary = () => {
  const [titleShow, setTitleShow] = useState(false);
  const [currentId, setCurrentId] = useState(null);
  const dispatch = useDispatch();
  const librariesItemsData = useSelector(librariesItems);
  console.log('librariesItemsData:', librariesItemsData);

  useEffect(() => {
    dispatch(getUserPostRequest());
  }, []);

  const buttonOnClick = useCallback(() => {
    dispatch(userLogout());
  }, []);

  const currentLibriryCard =
    currentId !== null ? librariesItemsData[currentId] : null;

  return (
    <div className="container">
      {titleShow && (
        <Modal close={setTitleShow}>
          <LibraryCard data={currentLibriryCard} closeCard={setTitleShow} />
        </Modal>
      )}
      <div className={style.header}>
        <UserInfoBlock />
        <Button typeStyle={bts.CANCEL} text="" onClick={buttonOnClick} />
      </div>
      <div className={style.library}>
        <div className={style.block}>
          <Button
            className={style.btnHidden}
            text="+"
            onClick={() => {
              setCurrentId(null);
              setTitleShow(true);
            }}
          />
        </div>
        
        {librariesItemsData.map((item, index) => (
          <ItemLibraryCard
            src={item.image}
            key={item.id}
            onClick={() => {
              setCurrentId(index);
              setTitleShow(true);
            }}
          />
        ))}
      </div>
    </div>
  );
};
// `https://api-anime-library.herokuapp.com${item.image}`
