import { memo } from 'react';
import pt from 'prop-types';
import cn from 'classnames';
import style from './Button.module.css';

export const Button = memo(
  ({ text, className, onClick, typeStyle, submit }) => {
    return (
      <button type={submit ? 'submit' : 'button'}
        className={cn(style.button, style[typeStyle], className)}
        onClick={onClick}
      >
        {text}
      </button>
    );
  }
);

Button.propTypes = {
  text: pt.string,
  className: pt.string,
  onClick: pt.func,
  typeStyle: pt.string,
  submit: pt.bool,
};

Button.defaultProps = {
  text: 'Button',
  className: '',
  onClick: () => {},
  typeStyle: '',
  submit: false,
};
