import { useState, useCallback } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import { useForm } from 'react-hook-form';
import cn from 'classnames';
import { Input } from '../UI/Input/Input';
import { Button } from '../UI/Button/Button';
import { ButtonTypeStyle as bts } from '../UI/Button/Button.utils';
import { userLogin } from '../../Models/UserAuth/userAuth.actions';
import { validateOptions } from './LoginForm.utils';
import { errorsHelper } from '../../utils/helpers/errorsHelper';
import style from './LoginForm.module.css';
import { authError } from '../../Models/UserAuth/userAuth.selectors';

export const LoginForm = () => {
  const [login, setLogin] = useState('');
  const [password, setPassword] = useState('');

  const error = useSelector(authError);
  const dispatch = useDispatch();
  const navigate = useNavigate();
  console.log('error:', error);

  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm({ mode: 'onBlur' });

  const changeLogin = useCallback(
    e => {
      setLogin(e.target.value);
    },
    [setLogin]
  );

  const changePassword = useCallback(
    e => {
      setPassword(e.target.value);
    },
    [setPassword]
  );

  const buttonOnClick = useCallback(() => {
    navigate('/registration');
  }, []);

  const buttonOnClickNavigate = useCallback(() => {
    navigate('/');
  }, []);

  const sendLoginRequest = data => {
    dispatch(userLogin({ login: data.username, password: data.password }));
  };
  const errorsData = errorsHelper(errors, validateOptions, style);

  return (
    <div className={style.cont}>
      <div className={style.buttonGroup}>
        <Button
          typeStyle={style.buttonSwitch}
          text=""
          onClick={buttonOnClick}
        />
        <Button
          typeStyle={style.buttonSwitch}
          text=""
          onClick={buttonOnClickNavigate}
        />
      </div>
      <form
        className={style.loginForm}
        onSubmit={handleSubmit(sendLoginRequest)}
      >
        <p className={style.title}></p>
        <Input
          name="username"
          register={register}
          value={login}
          placeholder=""
          className={cn(style.input, {
            [style.isError]: errorsData['username'],
          })}
          onChange={changeLogin}
          validateOption={validateOptions.username}
        />
        {errorsData['username']}

        <Input
          name="password"
          register={register}
          value={password}
          type="password"
          placeholder=""
          className={cn(style.input, {
            [style.isError]: errorsData['password'],
          })}
          onChange={changePassword}
          validateOption={validateOptions.password}
        />
        {errorsData['password']}

        <div className={style.btnGroup}>
          <Button
            typeStyle={bts.SUCCESS}
            text=""
            onClick={buttonOnClick}
          />
          <Button typeStyle={bts.GREENSUCCESS} text="" submit />
        </div>
      </form>
    </div>
  );
};
