import { memo } from 'react';
import pt from 'prop-types';
import cn from 'classnames';
import style from './Input.module.css';
/* eslint-disable */

export const Input = memo(
  ({
    type,
    placeholder,
    onChange,
    className,
    register,
    name,
    validateOption,
    disabled,
    typeStyle,
    value,
    onInput
  }) => {
    // const registerProp = register && { ...register(name, validateOption) };
    return (
      <input
        {...register(name, validateOption)}
        autoComplete="new-password"
        type={type}
        className={cn(style.input, style[typeStyle], className)}
        placeholder={placeholder}
        onChange={onChange}
        disabled={disabled}
        value={value}
        onInput={onInput}
      />
    );
  }
);

Input.propTypes = {
  type: pt.string,
  placeholder: pt.string,
  className: pt.string,
  onChange: pt.func,
  register: pt.func,
  name: pt.string,
  validateOption: pt.object,
  typeStyle:pt.string,
  disabled:pt.bool,
  value:pt.string,
};

Input.defaultProps = {
  type: 'input',
  placeholder: '',
  className: '',
  onChange: () => {},
  name: '',
  validateOption: {},
  typeStyle:'',
  register: () => {},
  disabled: false,
  value:'',
};
